import React, { useState } from 'react';
import { makeStyles , withStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import Avatar from '@material-ui/core/Avatar';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import Badge from '@material-ui/core/Badge';
import avatarimg from "../images/avatar.jpg"
import HomeImage from "../images/home.svg"
import HomeRoundedIcon from '@material-ui/icons/HomeRounded';
import SendRoundedIcon from '@material-ui/icons/SendRounded';
import ExitToAppRoundedIcon from '@material-ui/icons/ExitToAppRounded';
import { Divider } from '@material-ui/core';


const StyledBadge = withStyles((theme) => ({
    badge: {
      backgroundColor: '#44b700',
      color: '#44b700',
      boxShadow: `0 0 0 2px ${theme.palette.background.paper}`,
      '&::after': {
        position: 'absolute',
        top: 0,
        left: 0,
        width: '100%',
        height: '100%',
        borderRadius: '50%',
        animation: '$ripple 1.2s infinite ease-in-out',
        border: '1px solid currentColor',
        content: '""',
      },
    },
    '@keyframes ripple': {
      '0%': {
        transform: 'scale(.8)',
        opacity: 1,
      },
      '100%': {
        transform: 'scale(2.4)',
        opacity: 0,
      },
    },
  }))(Badge);
  
  const SmallAvatar = withStyles((theme) => ({
    root: {
      width: 22,
      height: 22,
      border: `2px solid ${theme.palette.background.paper}`,
    },
  }))(Avatar);


const useStyles = makeStyles((theme) => ({
    root: {
      width: '100%',
      maxWidth: 280,
      zIndex: "-1",
      background:"#ffffff",
      height:"93vh",
      position:" fixed",
      bottom: "0%",
      left: "0",
      display: "flex",
      flexDirection: "column",
      justifyContent: "space-around"
    },
    social:{
        textAlign: "center",
        marginTop: "10px",
        fontSize:"9px",
        fontWeight: "700",
        marginBottom:"8px"
    },
    sizeIcon:{
        width:"40px",
        height:"40px",
        color:"white",
        backgroundColor:theme.palette.background.paper,
        
    },
    iconDefault:{
        fontSize:"20px",
        fontWidth:"700",
        "&:span":{
            fontSize:"50px"
        }
    },
    sizeList:{
      display: "flex",
      justifyContent: "center",
      marginRight: "-11px",
      marginTop: "0px"
    },
    sizeList1:{
      display: "flex",
      justifyContent: "center",
      marginRight: "-11.5px",
      marginTop: "0px",
    },
    LogoutSize:{
        minWidth: "31px",
        display:"flex",
        alignItems:" center",
        color:"#acacac",
        "&:hover":{
          color:"#fe446d",
          borderLeft:"5px solid #fe446d"
        }
    },
    AvatarImage:{
        display: 'flex',
        justifyContent:" center",
        marginTop:"10px",
    '& > *': {
      margin: theme.spacing(1),
    },
    },
    sizeImage:{
        marginTop:"10px",
        width:"80px",
        height:"80px",
        // boxShadow:"  0px 0px 1px 2px #fff , 0px 0px 3px 6px #d81b60",
        boxShadow: "0px 0px 0px 2px #fff , 0px 0px 0px 4px #e832a0" 
    },
    spanSize:{
        fontSize: "16px",
        marginTop:"4px",

    },
    IconDivider:{
        width:"45%"
    },
    divAling:{
      display: "contents"
    },
    border:{
      color:"#acacac",
      "&:hover":{
        color:"#fe446d",
        borderLeft:"5px solid #fe446d"
      }
    },
    divSeting:{
      marginBottom:"400px"
    }
    
  }));

  const FocusHandler = (event) => {
    console.log("hi");
  }


const DividerUi = (props) => {
  

  const classes = useStyles();
  return (
      <>
      
          <List component="nav" className={classes.root} aria-label="mailbox folders">
          <div className={classes.divSeting}>
          <div className={classes.AvatarImage}>
          <StyledBadge 
              overlap="circular"
              anchorOrigin={{
              vertical: 'bottom',
              horizontal: 'right',
              }}
              variant="dot"
          >
              <Avatar className={classes.sizeImage} alt="Remy Sharp" src={avatarimg} />
          </StyledBadge>
          </div>
          <div className={classes.divAling}>
          <p className={classes.social}>{props.userName}</p>
          </div>
          <ListItem className={classes.border}  button >
              <ListItemAvatar className={classes.sizeList}>
                      <HomeRoundedIcon src={HomeImage} className={classes.IconDivider} />
              </ListItemAvatar>
                  <span  className={classes.spanSize}>Home</span>
          </ListItem>
          <ListItem className={classes.border} button  >
          <ListItemAvatar className={classes.sizeList1}>
                          <SendRoundedIcon  className={classes.IconDivider} />
              </ListItemAvatar>
                  <span className={classes.spanSize} >Direct</span>
          </ListItem>
          <ListItem className={classes.border} button  >
          <ListItemAvatar className={classes.sizeList1}>
                          <SendRoundedIcon  className={classes.IconDivider} />
              </ListItemAvatar>
                  <span className={classes.spanSize} >CNS</span>
          </ListItem>
          </div>
          <div>
            <Divider />
          <ListItem button   className={classes.LogoutSize}>
          <ListItemAvatar className={classes.sizeList}>
                          <ExitToAppRoundedIcon  className={classes.IconDivider} />
              </ListItemAvatar>
              <span className={classes.spanSize}>Logout</span>
          </ListItem>
          </div>
          </List>
      </>
  );
}

export default DividerUi;