import React, { useEffect, useState , axios } from "react";
import Web3 from "web3";
import Navbar from "./screens/Navbar";
import { networks } from "./screens/Network.js";
import "./styles/index.css";
import Main from "./screens/Main";
import Decentragram from "./abis/Decentragram.json";
import styled from "styled-components";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import LandingPage from "./screens/LandingPage";

const Wrapper = styled.div`
  display: flex;

  align-items: center;
  justify-content: center;
  background-color: #fff;
  height: 100vh;
`;

const Loading = styled.img`
  width: 100px;
  height: 100px;
`;

export default function App() {
  const [account, setAccount] = useState(null);
  const [network, setNetwork] = useState('');
  const [currentAccount, setCurrentAccount] = useState('');
  const [images, setImages] = useState([]);
  const [decetragram, setDecetragram] = useState(null);
  const [buffer, setBuffer] = useState(null);
  const [isLoading, setIsLoading] = useState(true);
  const [fileImg, setFileImg] = useState(null);

  
  useEffect(async () => {
    checkWallet();
    await loadBlockchainData();
  }, []);



  const checkWallet = async () => {
    const { ethereum } = window;

		if (!ethereum) {
			console.log('Make sure you have metamask!');
			return;
		} else {
			console.log('We have the ethereum object', ethereum);
		}

		// Check if we're authorized to access the user's wallet
		const accounts = await ethereum.request({ method: 'eth_accounts' });

		// Users can have multiple authorized accounts, we grab the first one if its there!
		if (accounts.length !== 0) {
			const account = accounts[0];
			console.log('Found an authorized account:', account);
			setCurrentAccount(account);
		} else {
			console.log('No authorized account found');
		}

    //checking the user's network chain ID
		const chainId = await ethereum.request({ method: 'eth_chainId' });
		setNetwork(network[chainId]);

		ethereum.on('chainChanged', handleChainChanged);
		
		// Reload the page when they change networks
		function handleChainChanged(_chainId) {
			window.location.reload();
		}
  };

  const loadBlockchainData = async () => {
    const web3 = new Web3(window.ethereum);
    // Load account
    const accounts = await web3.eth.getAccounts();
    setAccount(accounts[0]);

    // Network ID
    const networkId = 53;
    const networkData = Decentragram.networkId;
    console.log(networkData);
    if (networkData) {
      const decentragram = new web3.eth.Contract(
        Decentragram.abi,
        "0x535C84d180aEd4D4F879200f94d062fc5bA27EA7"
      );
      setDecetragram(decentragram);
      const imagesCount = await decentragram.methods.postCount().call();
      for (let i = 1; i <= imagesCount; i++) {
        const image = await decentragram.methods.images(i).call();
        setImages((prevState) => [...prevState, image]);
      }

      setIsLoading(false);
    } else {
      window.alert("Decentragram contract not deployed to detected network.");
    }
  };

  const switchNetwork = async () => {
    if (window.ethereum) {
      try {
        // Try to switch to the Mumbai testnet
        await window.ethereum.request({
          method: 'wallet_switchEthereumChain',
          params: [{ chainId: '0x35' }], // Check networks.js for hexadecimal network ids
        });
      } catch (error) {
        // This error code means that the chain we want has not been added to MetaMask
        // In this case we ask the user to add it to their MetaMask
        if (error.code === 4902) {
          try {
            await window.ethereum.request({
              method: 'wallet_addEthereumChain',
              params: [
                {	
                  chainId: '0x35',
                  chainName: 'Coinex Smart Chain Testnet',
                  rpcUrls: ['https://testnet-rpc.coinex.net/'],
                  nativeCurrency: {
                      name: "tCET",
                      symbol: "CET",
                      decimals: 18
                  },
                  blockExplorerUrls: ["https://testnet.coinex.net/"]
                },
              ],
            });
          } catch (error) {
            console.log(error);
          }
        }
        console.log(error);
      }
    } else {
      // If window.ethereum is not found then MetaMask is not installed
      alert('MetaMask is not installed. Please install it to use this app: https://metamask.io/download.html');
    } 
  }

  return (
    <>
      {account ? (
        <>
          <Navbar account={account} decetragram={decetragram} />
          <Main
            userName={account}
            images={images.sort((a, b) => b.tipAmount - a.tipAmount)}
            account={account}
            decetragram={decetragram}
          />
        </>
      ) : (
        <LandingPage />
      )}
      <ToastContainer />
    </>
  );
}
